@use "variables";

// Scaffolding

* {
  &,
  &:before,
  &:after {
    box-sizing: border-box;
  }
}

html {
  font-size: 16px;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: variables.$font-family-primary;
  line-height: variables.$line-height-s;
  color: variables.$text-color;
  background-color: #fff;
  border-top: variables.$header-height-mobile solid variables.$header-bg;
  overflow-x: hidden;

  @media (min-width: variables.$screen-s-min) {
    overflow-x: initial;
  }

  @media (min-width: variables.$header-breakpoint) {
    border-top-width: variables.$header-height;
  }
}

a,
.link {
  color: currentColor;
  text-decoration: underline;

  &:hover,
  &:focus {
    text-decoration: none;
  }
}

.link-inverted,
.links-inverted a {
  text-decoration: none;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.link-no-decoration {
  text-decoration: none !important;
}

hr {
  display: none;
}
