@use "sass:map";
@use "variables";
@use "mixins";

// Utilities

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
}

img {
  max-width: 100%;
  height: auto;
}

// No scroll
.no-scroll {
  overflow: hidden !important;
}

// Font
.font {
  &-primary {
    font-family: variables.$font-family-primary !important;
  }
  &-sans-serif {
    font-family: variables.$font-family-sans-serif !important;
  }
}

// Text
@include mixins.breakpoint-iterator(variables.$font-sizes, "fs", ("font-size"));
@include mixins.breakpoint-iterator(variables.$text-align, "text", ("text-align"));
@include mixins.breakpoint-iterator(variables.$font-weights, "fw", ("font-weight"));

.text {
  // Transform
  &-lowercase {
    text-transform: lowercase;
  }

  &-uppercase {
    text-transform: uppercase;
  }

  // Ellipsis
  &-ellipsis {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  // Line clamp
  &-clamp {
    display: -webkit-box;
    overflow: hidden;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: var(--clamp-lines, 3);
  }

  // Wrap
  &-nowrap {
    white-space: nowrap;
  }

  // Color
  &-white {
    color: #fff !important;
  }

  &-black {
    color: #000 !important;
  }

  &-danger {
    color: variables.$danger-color !important;
  }

  &-success {
    color: variables.$success-color !important;
  }

  &-gray {
    &-100 { color: variables.$gray-100 !important; }
    &-200 { color: variables.$gray-200 !important; }
    &-300 { color: variables.$gray-300 !important; }
    &-400 { color: variables.$gray-400 !important; }
    &-500 { color: variables.$gray-500 !important; }
    &-600 { color: variables.$gray-600 !important; }
    &-700 { color: variables.$gray-700 !important; }
    &-800 { color: variables.$gray-800 !important; }
    &-900 { color: variables.$gray-900 !important; }
  }

  &-muted {
    color: variables.$gray-500 !important;
  }
}

// Ellipsis
.ellipsis {
  @include mixins.ellipsis();
}

// Display
@include mixins.breakpoint-iterator(variables.$display, "d", ("display"));

// Background color
@include mixins.rule-iterator(variables.$grays, '', 'bg-gray', 'background-color');
@include mixins.rule-iterator(variables.$colors, '', 'bg', 'background-color');

// Spacing
@include mixins.breakpoint-iterator(variables.$spacers, "mt", ("margin-top"), $negative: true);
@include mixins.breakpoint-iterator(variables.$spacers, "mb", ("margin-bottom"), $negative: true);
@include mixins.breakpoint-iterator(map.merge(variables.$spacers, ("auto": auto)), "m", ("margin"));
@include mixins.breakpoint-iterator(map.merge(variables.$spacers, ("auto": auto)), "mt", ("margin-top"));
@include mixins.breakpoint-iterator(map.merge(variables.$spacers, ("auto": auto)), "mb", ("margin-bottom"));
@include mixins.breakpoint-iterator(map.merge(variables.$spacers, ("auto": auto)), "ml", ("margin-left"));
@include mixins.breakpoint-iterator(map.merge(variables.$spacers, ("auto": auto)), "mr", ("margin-right"));
@include mixins.breakpoint-iterator(variables.$spacers, "p", ("padding"));
@include mixins.breakpoint-iterator(variables.$spacers, "pt", ("padding-top"));
@include mixins.breakpoint-iterator(variables.$spacers, "pb", ("padding-bottom"));
@include mixins.breakpoint-iterator(variables.$spacers, "pl", ("padding-left"));
@include mixins.breakpoint-iterator(variables.$spacers, "pr", ("padding-right"));

// Width
@include mixins.breakpoint-iterator(variables.$sizes, "w", ("width"));

// Max width
@include mixins.breakpoint-iterator(variables.$sizes, "mw", ("max-width"));

// Height
@include mixins.breakpoint-iterator(variables.$sizes, "h", ("height"));

// Gap
[class*="gy-"] {
  margin-top: calc(var(--gap-y) * -1);

  > * {
    margin-top: var(--gap-y);
  }
}
@include mixins.breakpoint-iterator(variables.$spacers, "gy", ("--gap-y"));

// Shadow
@include mixins.breakpoint-iterator(variables.$shadows, "shadow", ("filter"));
@include mixins.breakpoint-iterator(variables.$box-shadows, "box-shadow", ("box-shadow"));
@include mixins.breakpoint-iterator(variables.$text-shadows, "text-shadow", ("text-shadow"));

// Opacity
@include mixins.rule-iterator(variables.$opacity, '', 'opacity', 'opacity');

// Z-index
@include mixins.rule-iterator(variables.$z-index, '', 'zindex', 'z-index');

// Border radius
@include mixins.rule-iterator(variables.$border-radius, '', 'rounded', 'border-radius');

// Position
.relative {
  position: relative;
}
