@use "variables";
@use "mixins";

// Typography

@font-face {
  font-family: 'goudy_mediaeval';
  src: url('fonts/goudy_mediaeval_regular.woff2') format('woff2'),
  url('fonts/goudy_mediaeval_regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'goudy_mediaeval';
  src: url('fonts/goudy_mediaeval_bold.woff2') format('woff2'),
  url('fonts/goudy_mediaeval_bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}


h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4 {
  font-family: variables.$font-family-primary;
  font-weight: variables.$font-weight-normal;
  line-height: variables.$line-height-heading;
}

h1,
.h1 {
  margin-top: 1.25em;
  margin-bottom: 1.25em;
  @include mixins.fluid-type(variables.$container-min-width, variables.$container-max-width, 2.5rem, 3.5rem);
  font-weight: variables.$font-weight-bold;
  color: variables.$text-color;
}

h2,
.h2 {
  margin-top: 1em;
  margin-bottom: .5em;
  @include mixins.fluid-type(variables.$container-min-width, variables.$container-max-width, 2rem, 2.25rem);
  color: variables.$text-color;
}

h3,
.h3 {
  margin-top: 1em;
  margin-bottom: .5em;
  @include mixins.fluid-type(variables.$container-min-width, variables.$container-max-width, 1.5rem, 1.75rem);
  color: variables.$text-color;
}

h4,
.h4 {
  margin-top: 1em;
  margin-bottom: .5em;
  @include mixins.fluid-type(variables.$container-min-width, variables.$container-max-width, variables.$font-size-s, 1.25rem);
  font-weight: variables.$font-weight-bold;
}

.display {
  &-1 {
    @include mixins.fluid-type(variables.$container-min-width, variables.$container-max-width, 3rem, 5.5rem);
    font-weight: variables.$font-weight-bold;
  }

  &-2 {
    @include mixins.fluid-type(variables.$container-min-width, variables.$container-max-width, 2.5rem, 4.75rem);
  }

  &-3 {
    @include mixins.fluid-type(variables.$container-min-width, variables.$container-max-width, 2rem, 4rem);
  }
}
